import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import {
  IChatSchedulingAddVM,
  ITeacherTimeTableVM,
} from "app/teacher/models/teacher-chats";

@Injectable({
  providedIn: "root",
})
export class TeacherChatService {
  constructor(private http: HttpClient) {}

  addChatScheduling(data: IChatSchedulingAddVM) {
    return this.http.post(`${environment.baseUrl}AskTeacherSchedulings`, data);
  }

  // get teacher chat time table
  getTeacherChatScheduling() {
    return this.http.get(
      `${environment.baseUrl}AskTeacherSchedulings/GeTAskTeacherScheduling`
    );
  }

  getTeacherSubjectIds() {
    return this.http.get(
      environment.baseUrl + "AJAX/returnTeacherSubjectIdsForAssistant"
    );
  }

  SendTeacherMessage(data: any) {
    return this.http.post(
      environment.baseUrl + "MessageThreads/SendMessage",
      data
    );
  }

  closeMsg(msgId: number) {
    return this.http.post(
      `${environment.baseUrl}AJAX/CloseThread/${msgId}`,
      null
    );
  }

  removeReply(msgId: number) {
    return this.http.delete(environment.baseUrl + "AJAX/RemoveReply/" + msgId);
  }

  createMessageThreads(body: any) {
    return this.http.post(environment.baseUrl + "MessageThreads", body);
  }

  updateMessageThreads(body: any, ThreadId: number) {
    return this.http.put(
      `${environment.baseUrl}MessageThreads/${ThreadId}`,
      body
    );
  }
}
