import { GeneralService } from "app/shared/services/General/general.service";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  ClassRoomGetVM,
  IStudentClassGetVM,
} from "app/dashboard/models/setup/general-setup/classes/class-room";
import { StudentService } from "app/dashboard/services/setup/student/student.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";
import { TeacherService } from "app/teacher/services/teacher.service";
import { AppreciationsService } from "app/dashboard/services/setup/general-setup/appreciations.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { IExpressioModel } from "app/dashboard/models/setup/general-setup/appreciation-expressions";
import { IAppreciationSendVM } from "app/shared/models/general/general";

@Component({
  selector: "app-appreciation",
  templateUrl: "./appreciation.component.html",
  styleUrls: ["./appreciation.component.scss"],
})
export class AppreciationComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;

  classroomList: ClassRoomGetVM[] = [];
  expressionsList: IExpressioModel[] = [];
  studentList: any[] = [];

  currentLang: string;
  EduCompId: number;
  functionId: number;

  constructor(
    private fb: FormBuilder,
    private teacherService: TeacherService,
    private AppreciationExpressionServ: AppreciationsService,
    private StudentServ: StudentService,
    private generalServ: GeneralService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      // id: [0],
      classroomId: ["", [Validators.required]],
      studentIds: [
        {
          value: [],
          disabled: true,
        },
        [Validators.required],
      ],
      expression: [
        "" /* , [Validators.required, CustomeValidator.whiteSpace] */,
      ],
      AppreciationExpressionId: ["", [Validators.required]],
    });

    this.getAllClassroomsOfTeacher();
    this.getAllAppreciationExpressions();
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllClassroomsOfTeacher();
      this.getAllAppreciationExpressions();
    });
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get Classrooms >> dropDown
  getAllClassroomsOfTeacher() {
    this.spinner.show();
    this.teacherService.getClassesOfTeacher().subscribe((res) => {
      if (res) this.classroomList = res;
      this.spinner.hide();
    });
  }

  // get Appreciation Expression >> dropDown
  getAllAppreciationExpressions() {
    this.spinner.show();
    this.AppreciationExpressionServ.getAllExpressionsDropDown().subscribe(
      (res) => {
        console.log("resss", res);
        if (res) this.expressionsList = res;
        this.spinner.hide();
      }
    );
  }

  // get students in classroom
  onChangeClassroom(classroomId: number) {
    console.log("cclll", classroomId);
    this.formCtrls?.studentIds.setValue(null);

    if (classroomId) {
      this.spinner.show();
      this.StudentServ.GetAllStudentsByClassroomId(classroomId).subscribe(
        (res) => {
          if (res) {
            console.log("rrrrr", res);
            this.studentList = res;
            this.formCtrls?.studentIds.enable();
          }
          this.spinner.hide();
        }
      );
    } else this.formCtrls?.studentIds.disable();
  }

  // send appreciation
  onSubmit() {
    this.submitted = true;
    // check to send each expression
    if (!this.formCtrls.AppreciationExpressionId?.value) {
      this.formCtrls.expression.setValidators([
        Validators.required,
        CustomeValidator.whiteSpace,
      ]);
      this.formCtrls.expression.updateValueAndValidity();
    } else {
      this.formCtrls.expression.clearValidators();
      this.formCtrls.expression.updateValueAndValidity();
    }

    if (this.myForm.valid) {
      let appreciation: IAppreciationSendVM = {
        // eduCompId: this.EduCompId,
        ToUsers: this.formCtrls.studentIds?.value,
        //FreeText: this.formCtrls.expression?.value,
        AppreciationExpressionId:
          this.formCtrls.AppreciationExpressionId?.value,
      };
      console.log("appreciation", appreciation);

      this.spinner.show();
      this.generalServ.sendAppreciation(appreciation).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.ToastrMsg.error("General.SendSuccessfully");
        } else this.ToastrMsg.error(res?.returnString);
        this.spinner.hide();
      });
    } else this.ToastrMsg.error("General.FormNotValid");
  }
}
