import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";

import { TeacherRoutingModule } from "./teacher-routing.module";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { SharedModule } from "app/shared/shared.module";
import { MaterialModule } from "app/shared/material/material.module";
import { NgbModule } from "@ng-bootstrap/ng-bootstrap";
import { NgxSpinnerModule } from "ngx-spinner";
import { DateAdapter, MAT_DATE_FORMATS } from "@angular/material/core";
import {
  AppDateAdapter,
  APP_DATE_FORMATS,
} from "app/shared/validators/date.adapter";
import { AppreciationComponent } from "./pages/appreciation/appreciation.component";
import { ChatSchedulingComponent } from "./pages/chat-scheduling/chat-scheduling.component";
import { ChatTimeTableComponent } from "./pages/chat-time-table/chat-time-table.component";
import { TeacherChatsComponent } from "./pages/teacher-chats/teacher-chats.component";

@NgModule({
  declarations: [
    AppreciationComponent,
    ChatSchedulingComponent,
    ChatTimeTableComponent,
    TeacherChatsComponent,
  ],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    SharedModule,
    MaterialModule,
    NgbModule,
    NgxSpinnerModule,
    TeacherRoutingModule,
  ],
  providers: [
    {
      provide: DateAdapter,
      useClass: AppDateAdapter,
    },
    {
      provide: MAT_DATE_FORMATS,
      useValue: APP_DATE_FORMATS,
    },
  ],
})
export class TeacherModule {}
