import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { ActivatedRoute, Router } from "@angular/router";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IfileVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GeneralService } from "app/shared/services/General/general.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import {
  messageThreadVM,
  MsgReplayVM,
  MsgsVM,
} from "app/students/models/student-chats";
import { StudentChatService } from "app/students/services/student-chats.service";
import { TeacherChatService } from "app/teacher/services/teacher-chats.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-chats",
  templateUrl: "./teacher-chats.component.html",
  styleUrls: ["./teacher-chats.component.scss"],
})
export class TeacherChatsComponent implements OnInit {
  MsgReplaies: MsgReplayVM[] = []; //List of replaies For Msg
  TeacherSubjectIds: any;
  selectedMsgToReplay: MsgsVM = null;
  currentUserId: number;
  attachecFilePath: any = null;
  replayForm: FormGroup;
  createThreadForm: FormGroup;

  schedulingId: any;
  msgGroup?: messageThreadVM = null;
  creatSubmitted? = false;
  currentThreadId: any = null;
  isContentOverlay = false;

  constructor(
    private fb: FormBuilder,
    private teacherChatService: TeacherChatService,
    private studentChatService: StudentChatService,
    public uploadFileService: UploadFileService,
    private GeneralServ: GeneralService,
    private authService: AuthService,
    private toastr: MessagesService,
    private route: ActivatedRoute,
    private router: Router,
    public dialog: MatDialog,
    private spinner: NgxSpinnerService
  ) {
    this.route.paramMap.subscribe((params) => {
      this.schedulingId = params.get("SchedulingId");
      console.log("schedulingId:", this.schedulingId);
    });
    this.route.queryParamMap.subscribe((params) => {
      this.currentThreadId = params.get("ThreadId");
      console.log("currentThreadId:", this.currentThreadId);
    });

    this.currentUserId = authService.getUserId();
  }

  ngOnInit(): void {
    this.getTeacherSubjectIds();

    this.replayForm = this.fb.group({
      threadId: ["", Validators.required],
      TeacherSubjectIds: [[], Validators.required],
      ParentReplyId: [null],
      Attach: [null],
      ReplyText: [""],
    });

    this.createThreadForm = this.fb.group({
      askTeacherSchedulingId: [this.schedulingId, Validators.required],
      replyQueueAmount: [null, [Validators.required, CustomeValidator.bigZero]],
    });

    if (this.currentThreadId != null) {
      this.getMsgReplaies(this.currentThreadId);
      this.FormCtrl.threadId.setValue(this.currentThreadId);
      this.getCurrntThread();
    }
  }

  get FormCtrl() {
    return this.replayForm.controls;
  }

  get replyQueueAmountCtrl() {
    return this.createThreadForm.get("replyQueueAmount");
  }

  getTeacherSubjectIds() {
    this.teacherChatService.getTeacherSubjectIds().subscribe((res: any) => {
      if (res) {
        //  console.log("TeacherSubjectIds", res);
        this.FormCtrl.TeacherSubjectIds.setValue(res);
      }
      this.spinner.hide();
    });
  }

  getMsgReplaies(msgId: number) {
    this.spinner.show();
    this.studentChatService
      .getMsgReplaies(msgId)
      .subscribe((res: MsgReplayVM[]) => {
        this.MsgReplaies = res;
        // setTimeout(function () {
        //   $(".chat-box .chat-body").animate(
        //     { scrollTop: $(".chat-box .chat-body").prop("scrollHeight") },
        //     1000
        //   );
        // }, 1000);
        this.spinner.hide();
      });
  }

  ReplayToMessage() {
    this.spinner.show();
    if (this.replayForm.valid) {
      this.teacherChatService
        .SendTeacherMessage(this.replayForm.value)
        .subscribe((res: any) => {
          // console.log('ReplayToMessage', res);
          if (res.returnString == "Success") {
            this.getMsgReplaies(this.FormCtrl.threadId.value);
            this.FormCtrl.ReplyText.reset();
            this.FormCtrl.Attach.reset();
            this.closePreview();
            // $(".chat-box .chat-body").animate(
            //   { scrollTop: $(".chat-box .chat-body").prop("scrollHeight") },
            //   1000
            // );
          } else {
            this.toastr.error(res.returnString);
          }
          this.spinner.hide();
        });
    }
  }

  getCurrntThread() {
    this.studentChatService
      .getCurrntThread(this.currentThreadId, this.schedulingId)
      .subscribe((res: any) => {
        console.log("CurrntThread", res);
        this.msgGroup = res.messageThread;
        // this.createThreadForm.controls['Id'].setValue(this.currentThreadId);
        //this.enterNewThread(res.Id);
        //this.currentThreadId = res.Id;
      });
  }

  createMessageThread() {
    this.creatSubmitted = true;
    let valid = this.createThreadForm.valid;
    console.log(this.createThreadForm.value);
    if (valid) {
      this.spinner.show();
      this.teacherChatService
        .createMessageThreads(this.createThreadForm.value)
        .subscribe((res: any) => {
          console.log(res);
          this.router.navigate([], {
            relativeTo: this.route,
            queryParams: { ThreadId: res.Id },
            queryParamsHandling: "merge",
          });
          this.msgGroup = res;
          this.creatSubmitted = false;
          this.currentThreadId = res.Id;
          this.FormCtrl.threadId.setValue(res.Id);
          this.createThreadForm.controls["replyQueueAmount"].reset();
          this.getMsgReplaies(res.Id);
          this.toastr.success("General.AddSuccessfully");
          this.spinner.hide();
        });
    }
  }

  updateMessageThreads() {
    this.creatSubmitted = true;
    let valid = this.createThreadForm.valid;
    console.log(this.createThreadForm.value);
    if (valid) {
      this.spinner.show();
      this.teacherChatService
        .updateMessageThreads(this.createThreadForm.value, this.currentThreadId)
        .subscribe((res: any) => {
          console.log(res);
          if (res.Id == this.currentThreadId) {
            this.creatSubmitted = false;
            this.getMsgReplaies(res.Id);
            this.createThreadForm.controls["replyQueueAmount"].reset();
            this.toastr.success("General.UpdateSuccessfully");
          }
          // this.msgGroup = res;

          this.spinner.hide();
        });
    }
  }

  removeReply(msgId: number) {
    this.spinner.show();
    this.teacherChatService.removeReply(msgId).subscribe((res: any) => {
      //  console.log('removeReply', res);
      if (res.returnValue == 1) {
        this.toastr.success("  ");
        this.getMsgReplaies(this.FormCtrl.threadId.value);
      }
      this.spinner.hide();
    });
  }

  getParentReplyId(msg: MsgsVM) {
    this.FormCtrl.ParentReplyId.setValue(msg.Id);
    this.selectedMsgToReplay = msg;
    if (msg) {
      console.log("ParentReplyIdMsg", msg);
    }
    console.log(this.replayForm.value);
  }

  closePreview() {
    this.FormCtrl.Attach.setValue(null);
    this.FormCtrl.ParentReplyId.setValue("");
    this.selectedMsgToReplay = null;
    this.attachecFilePath = null;
  }

  // attache Replay Pic
  attacheReplayPic(event: any) {
    this.uploadFileService.uploadFile(event).then((file: IfileVM) => {
      setTimeout(() => {
        this.FormCtrl.Attach.setValue(file);
        this.attachecFilePath = file;
      }, 100);
    });
  }

  onSidebarToggle() {
    this.isContentOverlay = true;
  }

  onContentOverlay() {
    this.isContentOverlay = false;
  }

  openDeleteDialog(msg: MsgReplayVM): void {
    const dialogRef = this.dialog.open(DeleteDialogComponent, {
      data: {
        title: "General.ConfirmDelete",
        msg: msg.Message,
      },
    });
    dialogRef.afterClosed().subscribe((confirm) => {
      if (confirm) this.removeReply(msg.Id);
    });
  }
}
