import { TranslateService } from "@ngx-translate/core";
import { Component, OnInit } from "@angular/core";
import { TeacherChatService } from "app/teacher/services/teacher-chats.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GroupByService } from "app/shared/services/General/group-by.service";
import { ITeacherTimeTableVM } from "app/teacher/models/teacher-chats";

@Component({
  selector: "app-chat-time-table",
  templateUrl: "./chat-time-table.component.html",
  styleUrls: ["./chat-time-table.component.scss"],
})
export class ChatTimeTableComponent implements OnInit {
  teacherTimeTable: ITeacherTimeTableVM[];

  currentLang: string;
  EduCompId: number;
  functionId: number;

  constructor(
    private ChatServ: TeacherChatService,
    private GroupByService: GroupByService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getTecherScheduling();
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getTecherScheduling();
    });
  }

  getTecherScheduling() {
    this.spinner.show();
    this.ChatServ.getTeacherChatScheduling().subscribe(
      (res: ITeacherTimeTableVM[]) => {
        if (res) {
          console.log("res Groups", this.GroupByService.groupBy(res, "Day"));
          console.log("res", res);
          this.teacherTimeTable = this.GroupByService.groupBy(res, "Day");
        }
        this.spinner.hide();
      }
    );
  }
}
