import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { ClassRoomGetVM } from "app/dashboard/models/setup/general-setup/classes/class-room";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ClassRoomService } from "app/dashboard/services/setup/general-setup/classes/class-room.service";
import { TeacherChatService } from "app/teacher/services/teacher-chats.service";
import { IChatSchedulingAddVM } from "app/teacher/models/teacher-chats";
import { WeeklyTableService } from "app/dashboard/services/educational-tabels/weekly-table.service";
import { IWeeklyDayVM } from "app/dashboard/models/educational-tabels/weekly-table";
import { TeacherService } from "app/teacher/services/teacher.service";

@Component({
  selector: "app-chat-scheduling",
  templateUrl: "./chat-scheduling.component.html",
  styleUrls: ["./chat-scheduling.component.scss"],
})
export class ChatSchedulingComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;

  currentLang: string;
  classroomList: ClassRoomGetVM[] = [];
  weeklyDays: IWeeklyDayVM[] = [];

  EduCompId: number;
  functionId: number;

  constructor(
    private fb: FormBuilder,
    private ChatServ: TeacherChatService,
    private teacherService: TeacherService,
    private WeeklyTableServ: WeeklyTableService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      // id: [0],
      classroomId: ["", [Validators.required]],
      weeklyDayId: ["", [Validators.required]],
      From_Hour: ["", [Validators.required]],
      To_Hour: ["", [Validators.required]],
    });

    this.getAllClassrooms();
    this.getAllWeeklyDays();
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllClassrooms();
      this.getAllWeeklyDays();
    });
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get Classrooms >> dropDown
  getAllClassrooms() {
    this.spinner.show();
    this.teacherService.getClassesOfTeacher().subscribe((res) => {
      if (res) this.classroomList = res;
      this.spinner.hide();
    });
  }

  // get Weekly Day
  getAllWeeklyDays() {
    this.spinner.show();
    this.WeeklyTableServ.getAllWeeklyDays().subscribe((res) => {
      if (res) this.weeklyDays = res;
      this.spinner.hide();
    });
  }

  // add chat scheduling
  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      if (this.formCtrls.To_Hour.value < this.formCtrls.From_Hour.value) {
        this.ToastrMsg.error("EducationalTabels.ToHourBeforeFromDHour");
        return;
      }

      let ChatScheduling: IChatSchedulingAddVM = {
        // eduCompId: this.EduCompId,
        classroomId: this.formCtrls.classroomId?.value,
        weeklyDayId: this.formCtrls.weeklyDayId?.value,
        From_Hour: this.formCtrls.From_Hour?.value,
        To_Hour: this.formCtrls.To_Hour?.value,
      };
      // console.log("ChatScheduling", ChatScheduling);

      this.spinner.show();
      this.ChatServ.addChatScheduling(ChatScheduling).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.ToastrMsg.error("General.AddSuccessfully");
        } else this.ToastrMsg.error(res?.returnString);
        this.spinner.hide();
      });
    } else this.ToastrMsg.error("General.FormNotValid");
  }
}
