import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class StudentChatService {
  constructor(private http: HttpClient) {}

  // Return askTeacher Data
  askTeacher(id: any) {
    return this.http.get(environment.baseUrl + "AJAX/askTeacher/" + id);
  }

  // Return Messages And replayes in msg
  getMsgReplaies(msgId: any) {
    return this.http.get(
      environment.baseUrl + "MessageThreads/returnThreadReplies/" + msgId
    );
  }

  //Replay to Msg
  SendStudentMessage(data: any) {
    return this.http.post(environment.baseUrl + "AJAX/SendMessage", data);
  }

  getStudentScheduling() {
    return this.http.get(
      `${environment.baseUrl}AskTeacherSchedulings/GeTAskTeacherSchedulingForStudent`
    );
  }

  getCurrntThread(threadId: any, SchedulId: any) {
    return this.http.get(
      `${environment.baseUrl}MessageThreads/${threadId}/${SchedulId}`
    );
  }

  likeMsg(body: any) {
    return this.http.post(`${environment.baseUrl}UserLikes`, body);
  }

  unLikeMsg(replyId: number) {
    return this.http.delete(`${environment.baseUrl}UserLikes/${replyId}`);
  }
}
