import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";

import { ChatSchedulingComponent } from "./pages/chat-scheduling/chat-scheduling.component";
import { ChatTimeTableComponent } from "./pages/chat-time-table/chat-time-table.component";
import { AppreciationComponent } from "./pages/appreciation/appreciation.component";
import { SendAppreciationComponent } from "app/shared/components/send-appreciation/send-appreciation.component";
import { TeacherChatsComponent } from "./pages/teacher-chats/teacher-chats.component";

const routes: Routes = [
  {
    path: "appreciation",
    component: SendAppreciationComponent,
  },
  {
    path: "chat-scheduling",
    component: ChatSchedulingComponent,
  },
  {
    path: "chat-table",
    component: ChatTimeTableComponent,
  },
  {
    path: "chats/:SchedulingId",
    component: TeacherChatsComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TeacherRoutingModule {}
